import React, { useRef, useState, useEffect, useCallback } from "react";


const ratio = 0.9


function useResizeObserver(ref, callback, isResize) {
  // 1. Custom Hook ResizeObserver  state 
  const [observer] = useState(
    new ResizeObserver((entries) => handleResize(entries))
  );

  // 2. Custom Hook   width, height, top, left
  const [width, setWidth] = useState();
  const [height, setHeight] = useState();
  const [top, setTop] = useState();
  const [left, setLeft] = useState();

  // 3. useCallback()     Callback  .
  const handleResize = useCallback(
    (entries) => {
      // 3-1.      return.
      if (!Array.isArray(entries)) {
        return;
      }
      
      // 3-2.        .
      const entry = entries[0];
      const { width, height, top, left } = entry.contentRect;
      if(width < 460 || height < 200){
        setWidth(460 * ratio);
        setHeight(200 * ratio);
        
      } else {
        if(width/height >=2.3){
          setWidth(height * 2.3 * ratio);
          setHeight(height * ratio);
        } else {
          setHeight((width * 0.43) * ratio); 
          setWidth(width * ratio);
        }
      }


      // 3-3.  width, height, top, left .
    //   setWidth(width);
    //   setHeight(height);
      setTop(top);
      setLeft(left);

      // 3-4.  .
      if (callback) {
        callback(width,height);
      }
    },
    [callback]
  );

  // 4. isResize    &.
  useEffect(() => {
    if (isResize) observer.observe(ref.current);
    else observer.unobserve(ref.current);
  }, [isResize]);  

  // 5.  width, height, top, left .
  return [width, height, top, left];
}

export default useResizeObserver;