import styled from "styled-components";
import { useRef, useEffect } from "react";
import {colorThemeList} from "./colorThemeList";
const ModalBasicBlock = styled.div`
    /*   */
    width: 500px;

    /*   */
    z-index: 999;
    font-family: Major Mono Display;
    opacity: ${props => props.modalOpen ? 1 : 0};
    visibility: ${props => props.modalOpen ? 'visible' : 'hidden'};
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.5s;
    /*   */
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    .header {
        font-size: 30px;
        padding: 0 15px;
        div {
            display: flex;
            align-items: center;
        }
        display: flex;
        justify-content: space-between;
        button {
            border: none;
            background: none;
            font-size: 20px;
            font-family: Major Mono Display;
            cursor: pointer;
            transition: all 0.3s ease;
            &:hover {
                scale: 1.3;
            }
        }                                    
    }

    .marginTop {
        margin-top: 30px;
    }
    .noiseSensitivityWrapper {
        .noiseSensitivityDisplay {
            padding: 20px;
            display: flex;
            justify-content: space-between;
            .display {
                width: 100px;
                height: 100px;
                background: #d9d9d9;
                border-radius: 10px;
                color: #E97777;
                display: flex;
                justify-content:center;
                align-items: center;
                font-size: 40px;
            }
            .slider {
                width: 300px;
                cursor: pointer;
            }
        }
    }
    .colorThemeWrapper {
        display: flex;
        justify-content: space-between;
        select {
            margin-right: 30px;
        }
    }
    .toggleWrapper {
        display: flex;
        margin-top: 30px;
        justify-content: space-between;
        margin-right: 30px;
    }
    .toggleSwitch {
        width: 50px;
        height: 25px;
        display: block;
        position: relative;
        border-radius: 30px;
        background-color: #fff;
        box-shadow: 0 0 16px 3px rgba(0 0 0 / 15%);
        cursor: pointer;
      }
      
      .toggleSwitch .toggleButton {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 50%;
        left: 4px;
        transform: translateY(-50%);
        border-radius: 50%;
        background: black;
      }
      
      #toggle:checked ~ .toggleSwitch {
        background: black;
      }
      
      #toggle:checked ~ .toggleSwitch .toggleButton {
        left: calc(100% - 22px);
        background: #fff;
      }
      
      .toggleSwitch, .toggleButton {
        transition: all 0.2s ease-in;
      }
`

const ModalBasic = ({modalOpen,setModalOpen, decibelData, setSensitivity, sensitivity, sensitivityRef, setTheme, setIsNumberVisible, isNumberVisible}) => {
    const modalRef = useRef(null);
    
    useEffect(() => {
        //   
        const handler = (e) => {
            // mousedown      ,   
            if (modalRef.current && !modalRef.current.contains(e.target)) {
                setModalOpen(false);
            }
        };
        
        //   
        document.addEventListener('mousedown', handler);
        // document.addEventListener('touchstart', handler); //  
        
        return () => {
            //   
            document.removeEventListener('mousedown', handler);
            // document.removeEventListener('touchstart', handler); //  
        };
    });

    const onChange = (e) => {
        setSensitivity(e.target.value);
        localStorage.setItem('sensitivity', e.target.value)
    }

    const themeChange = (e) => {
        if(e.target.value === "iron man"){
            setTheme(colorThemeList.ironman);
            localStorage.setItem('colorTheme','iron man');
        } else if(e.target.value === "black and white"){
            setTheme(colorThemeList.blackandwhite);
            localStorage.setItem('colorTheme','black and white');
        } else {
            setTheme(colorThemeList.vintage);
            localStorage.setItem('colorTheme','vintage')
        }
        console.log(e.target.value);
        console.log(colorThemeList.ironman);
        
    }
    return(
        <ModalBasicBlock modalOpen={modalOpen} ref={modalRef}>
            <div className="header">
                <div>
                    
                </div>
                <button className="closeBtn" onClick={()=>setModalOpen(false)}>x</button>
            </div>
            <div className="noiseSensitivityWrapper marginTop">
                <div></div>
                <div className="noiseSensitivityDisplay">
                    <div className="display">{Math.floor(decibelData)}</div>
                    <input className="slider" type="range" value={sensitivity} onChange={onChange} min={1} max={100} step={0.1} ref={sensitivityRef}></input>
                </div>
                <div style={{fontSize:'0.8rem', color: 'gray'}}>     .(---)</div> 
                <div style={{fontSize:'0.8rem', color: 'gray'}}>  100    1 .</div>
            </div>
            <div className="colorThemeWrapper marginTop">
                <div> </div>
                <select name="time" onChange={themeChange}>
                    <option value="vintage" ></option>
                    <option value="iron man"> </option>
                    <option value="black and white">  </option>
                </select>
            </div>
            <div className="toggleWrapper">
                <div> </div>
                <input type="checkbox" id="toggle" hidden /> 
                <label for="toggle" class="toggleSwitch" onClick={() => setIsNumberVisible(prev => !prev)}>
                <span class="toggleButton"></span>
                </label>
            </div>


        </ModalBasicBlock>
    )
}

export default ModalBasic;

